
.fade-enter-active,.fade-leave-active{-webkit-transition:opacity 1s;transition:opacity 1s
}
.fade-enter,.fade-leave-active{opacity:0
}
.vux-pop-out-transition,.vux-pop-in-transition{width:100%;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-fill-mode:both;animation-fill-mode:both;-webkit-backface-visibility:hidden;backface-visibility:hidden
}
.vux-pop-out-enter,.vux-pop-out-leave,.vux-pop-in-enter,.vux-pop-in-leave{will-change:transform;height:100%;position:absolute;left:0
}
.vux-pop-out-enter{-webkit-animation-name:popInLeft;animation-name:popInLeft
}
.vux-pop-out-leave{-webkit-animation-name:popOutRight;animation-name:popOutRight
}
.vux-pop-in-enter{-webkit-perspective:1000;perspective:1000;-webkit-animation-name:popInRight;animation-name:popInRight
}
.vux-pop-in-leave{-webkit-animation-name:popOutLeft;animation-name:popOutLeft
}


